<template>
  <div class="container min-vh-100" :class="{ 'pointers' : editing>=0 }">
    <div class="inner">
      <header class="d-flex align-items-center justify-content-center">
        todo app
      </header>
      <div ref="todoForm">
        <form class="d-flex align-items-center justify-content-center pb-2" autocomplete="off"
          action="javascript:void(0);" @submit.prevent="addTodo">
          <label for="todo" class="form-label d-none">Todo</label>
          <input class="form-control rounded-0" v-model.trim="todo" @blur="refresh" @click="scrollToTop" type="text"
            name="todo" id="todo" :placeholder="placeholder" :required="required" ref="addInput">
          <button class="btn btn-primary rounded-0 w-25" type="submit" name="button" ref="addButton">add</button>
        </form>
      </div>
      <div @click="locker" class="border-bottom d-flex justify-content-end" :class="{locked: isLocked}"><span class="pointer">{{isLocked ?'&#128274;':'&#128275;'}}</span></div>
      <div class="custom-table fs-5">
        <list-item v-for="(todo, index) in todoArray" :key="index" @local="addToLocalStorage" :todoL="todoLength()"
          @remove="removeTodo" @edit="editTodo" :item="todo" @priority="setImportance" @info="infoTodo" :index="index">
        </list-item>
      </div>
      <div v-if="!todoArray.length">All done, good job!</div>
      <div class="stats" v-if="todoArray.length">
        <div>total: {{todoArray.length}}</div>
        <div>left: {{counterTodo()}}</div>
        <div>numbers sum unchecked: {{counterNumbers()}}</div>
      </div>
    </div>
  </div>
  <div id="info" class="container-fluid hidden text-right" ref="info"
    @click="$event.currentTarget.classList.add('hidden')">
    <button type="button" class="btn-close p-2" aria-label="Close"></button>
    <div class="pt-2">
      <div class="mb-2 border p-2">
        <div class="fw-bold pe-2">Todo:</div>
        <div>{{itemInfo.todo}}</div>
      </div>
      <div class="mb-2 border p-2">
        <div class="fw-bold pe-2">Time added:</div>
        <div>{{itemInfo.timeAdd}}</div>
      </div>
      <div class="border p-2" v-if="typeof itemInfo.timeFin !=='undefined' && itemInfo.timeFin.length">
        <div class="fw-bold pe-2">Time finished:</div>
        <div>
          <div v-for="(oneItem, index) in itemInfo.timeFin" :key="index">{{oneItem}}<span
              @click="removeTime(itemInfo.index,index, $event)"><span
                class="text-danger pointer">&#9746;</span></span>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
  import listItem from './components/listItem'

  export default {
    name: 'App',
    data() {
      return {
        editing: -1, //hope that item index never be less then 0
        flag: false,
        importance: ['low', 'mid', 'hight'],
        lang: 'lt-LT',
        placeholder: 'Type here..',
        required: false,
        timeAdd: '',
        title: 'Todo-app',
        todo: '',
        isLocked: true,
        itemInfo: [],
        todoArray: [],
        todoLength: () => this.todoArray.length.toString().length
      }
    },
    components: {
      listItem
    },
    mounted() {
      this.todoArray = typeof (Storage) !== 'undefined' ? this.parser() || [] : []
      // if array empty focus todo input
      if (!this.todoArray.length) this.flag = true
    },
    watch: {
      flag() {
        if (this.flag) {
          window.setTimeout(() => {
            this.$refs.addInput.focus()
          }, 200)
        }
        this.flag = false
      }
    },
    methods: {
      locker: function () {
        this.isLocked = !this.isLocked;
      },
      removeTime(item, index, e) {
        e.stopPropagation()
        this.todoArray[item].timeFin.splice(index, 1)
        this.addToLocalStorage()
      },
      infoTodo(index) {
        this.$refs.info.classList.remove('hidden')
        this.itemInfo = {
          index,
          ...this.todoArray[index]
        }
      },
      parser() {
        try {
          return JSON.parse(window.localStorage.getItem('todo-app'))
        } catch (e) {
          // remove all wrong values and reload just in case to not break app
          window.localStorage.removeItem('todo-app')
          window.location.reload()
        }
      },
      counterTodo() {
        return this.todoArray.filter(todo => !todo.done).length
      },
      addToLocalStorage(index, e) {
        // if array exist (not deleted) set done at first
        if (this.todoArray[index] && e) {
          this.todoArray[index].done = e.target.checked
          // if done and minute passed time!==time (prevent from random clicking and writing time)
          // because we cant finish something in less than minute :)
          if (this.todoArray[index].done && this.todoArray[index].timeFin[this.todoArray[index].timeFin.length - 1] !=
            this.setTime()) {
            this.todoArray[index].timeFin.push(this.setTime())
          }
          // else delete this.todoArray[index].timeFin
        }
        // flash message in title for 800ms that it saved in local storage
        document.title = 'Todo – SAVED 💾'
        // set title back to Todo
        let tim = setTimeout(() => {
          document.title = 'Todo'
          clearTimeout(tim)
        }, 800)
        // rewrite all local storage
        typeof (Storage) !== 'undefined' && window.localStorage.setItem('todo-app', JSON.stringify(this.todoArray))
      },
      addTodo() {
        if (this.todo.trim().length === 0) {
          this.placeholder = 'Empty field!'
          this.required = true
          return
        }

        const values = [].concat.apply([], this.todoArray.filter(e => isNaN(e.todo)).map(e => e.todo))

        if (this.editing >= 0) {
          this.todoArray[this.editing].todo = this.todo
          this.$refs.addButton.innerHTML = 'add'
          this.$refs.todoForm.classList.remove('transforms')
          this.editing = -1
        } else {
          this.flag = true

          if (values.indexOf(this.todo.trim()) > -1) {
            this.todo = ''
            this.placeholder = 'Value already exists!'
            return
          }
          const string = this.todo.replace(/,/g, '.').trim().replace(/\s+/g, ' ').trim()
          this.todoArray.unshift({
            todo: string.charAt(0).toUpperCase() + string.slice(1),
            done: false,
            status: 'low',
            timeAdd: this.setTime(),
            timeFin: []
          })
        }
        this.todo = ''
        this.placeholder = 'Type here..'
        this.addToLocalStorage()
      },
      removeTodo(index) {
        this.todo = ''
        this.editing = -1
        this.todoArray.splice(index, 1)
        this.addToLocalStorage()
      },
      editTodo(index) {
        this.todo = this.todoArray[index].todo
        this.editing = index
        this.$refs.addButton.innerHTML = 'save'
        this.$refs.todoForm.classList.add('transforms')
        this.$refs.addInput.focus()
      },
      scrollToTop() {
        this.$refs.addInput.scrollIntoView({
          behavior: 'smooth'
        })
      },
      setImportance(index) {
        let priority = this.importance.indexOf(this.todoArray[index].status)
        if (++priority > 2) priority = 0
        this.todoArray[index].status = this.importance[priority]
        this.addToLocalStorage()
      },
      setTime() {
        const options = {
          hour: '2-digit',
          minute: '2-digit',
          hour12: false
        }
        return (new Date()).toLocaleDateString(this.lang, options)
      },
      counterNumbers() {
        const numArray = []
        // push only numbers to array
        this.todoArray.forEach(e => {
          if (typeof e.todo !== 'undefined' && !e.done && !isNaN(parseFloat(e.todo.match(
              /[+-]?((?=\.?\d)\d*\.?\d*)/g)))) {
            numArray.push(parseFloat(e.todo.match(/[+-]?((?=\.?\d)\d*\.?\d*)/g)))
          }
        })
        // return sum of numbers
        return numArray.reduce((a, b) => {
          const n1 = a.toString().split('.')[1]
          const n2 = b.toString().split('.')[1]
          const len1 = (n1 && n1.length) || 0
          const len2 = (n2 && n2.length) || 0
          return parseFloat((a + b).toFixed(len1 + len2))
        }, 0)
      }
    }
  }
</script>
<style scoped>
  .container {
    max-width: 800px;
    background-color: rgba(255, 255, 255, 0.8);
  }

  .custom-table {
    display: grid;
    box-sizing: border-box;
    grid-template-columns: 48px auto 48px 48px 48px 48px;
  }

  #info {
    position: fixed;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    background: #fff;
    overflow: scroll;
    text-align: right;
  }

  #info>div>div {
    display: flex;
    width: 100%;
  }

  #info>div>div {
    counter-reset: section;
  }

  #info>div>div>div:last-child {
    width: 100%;
  }

  #info>div>div:first-of-type {
    text-align: left;
  }

  #info>div>div>div:last-of-type div::before {
    counter-increment: section;
    content: counter(section)". ";
    font-size: 10px;
  }

  #info>div {
    display: flex;
    flex-direction: column;
    width: min-content;
    white-space: nowrap;
    margin: auto;
  }

  .hidden {
    display: none !important;
  }

  .stats {
    display: flex;
    flex-wrap: wrap;
    column-gap: 2em;
  }

  .inner {
    background-color: rgba(255, 255, 250, 0.7)
  }

  .form-control {
    scroll-margin-top: 1em;
  }

  button {
    text-transform: uppercase;
  }
</style>
<style>
  html {
    scroll-behavior: smooth;
  }
.locked ~ .custom-table{
      grid-template-columns: 48px auto 48px 48px 0 48px;
}  
/* .locked ~ .custom-table .trash {
  visibility: hidden;
  width: 0 !important;
} */
  .custom-table>div {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    border-bottom: 1px solid #eee;
    border-left: 1px solid #eee;
  }

  .custom-table>div:nth-of-type(6n+6) {
    border-right: 1px solid #eee;
  }

  body::after {
    content: "";
    display: block;
    height: 100%;
    width: 50%;
    clip-path: polygon(75% 0%, 100% 50%, 75% 100%, 0% 100%, 0% 0%);
    background: #aee;
    position: fixed;
    bottom: 0;
    left: 0;
    z-index: -1;
  }

  .transforms {
    position: fixed;
    top: 50%;
