    display: flex;
    place-content: center;
    height: 100%;
    width: 100%;
    background: #eee;
    transform: translate(-50%, -50%);
    z-index: 1;
    pointer-events: auto;
  }

  .pointers:not(form) {
    user-select: none;
    pointer-events: none;
  }

  .container label {
    text-overflow: ellipsis;
    overflow: hidden;
    display: block;
  }

  .container .form-check .form-check-input {
    margin: 0.25em;
  }

  .container .form-check {
    margin: 0;
    padding: 0;
  }

  .write {
    writing-mode: vertical-rl;
  }

  [colspan="6"] {
    text-align: center !important;
  }

  .pointer {
    cursor: pointer;
  }
</style>