<template>
  <div scope="row" class="text-center text-muted">{{(index+1).toString().padStart(Math.max(2,todoL), '0')}}</div>
  <div class="form-check">
    <input class="form-check-input pointer" @click="save(index, $event)" :checked="item.done?true:undefined"
      :id="'input-' + index" type="checkbox" value="" />
    <label class="form-check-label pointer" :class="{'done': item.done === true}"
      :for="'input-' + index">{{ item.todo}}</label>
  </div>
  <div class="flex-fill pointer fs-6" :class="item.status" @click="statusItem(index)">{{item.status}}</div>
  <div class="text-center flex-fill pointer" @click="editItem(index)">
    <div>&#128394;</div>
  </div>
  <div class="text-center flex-fill pointer trash" @click="removeItem(index)">
    <div class="text-danger" title="delete">&#9746;</div>
  </div>
  <div class="text-center flex-fill pointer" @click="infoItem(index)">
    <div>&#128712;</div>
  </div>
</template>
<script>
  export default {
    name: 'listItem',
    emits: ['priority', 'remove', 'edit', 'local', 'info'],
    props: {
      todoL: Number,
      item: Object,
      index: Number
    },
    methods: {
      statusItem(index) {
        this.$emit('priority', index)
      },
      removeItem(index) {
        this.$emit('remove', index)
      },
      editItem(index) {
        this.$emit('edit', index)
      },
      save(index, e) {
        this.$emit('local', index, e)
      },
      infoItem(index) {
        this.$emit('info', index)
      }
    }
  }
</script>
<style scoped>
  .flex-fill {
    display: flex;
    place-items: center;
    place-content: center;
  }

  .low {
    color: #6c757d;
  }

  .mid {
    color: #ffc107;
  }

  .hight {
    color: #dc3545;
  }

  .done {
    text-decoration: line-through;
  }
</style>